<template>
  <div class="flex-row" v-loading="loading">
    <tests-filter
    class="tests-filter"
    @keywordChange="toKeywordChange"
    @categoryChange="toCategoryChange"
    />
    <div class="flex-column flex-1 pd-1 ml-1">
      <div class="flex-row">
        <breadcrumb 
        :data="filter.category"
        @click="toClick"
        />
        <button class="ml-auto pure-button pure-button-primary" @click="toWorkshop">
          创建测试
        </button>
      </div>
      <no-data v-if="tests.length === 0 && !loading">
      </no-data>
      <ul v-else> 
        <li class="mt-1" v-for="item in tests" :key="item._id">
          <card :data="item"/>
        </li>
      </ul>
      <pager
        class="ml-auto mr-auto mt-auto"
        :total="pager.total"
        :len="10"
        :active="pager.page"
        @click="toPage"
      />
    </div>
  </div>
</template>
<script>
import debounce from '@/tools/debounce';
import config from '@/config';

import Card from './components/card.vue';
import TestsFilter from './components/filter.vue';

export default {
  components: {
    TestsFilter,
    Card,
  },
  data() {
    return {
      tests: [],
      loading: true,

      filter: {
        keyword: '',
        category: [],
      },

      pager: {
        page: 1,
        total: 0,
      }
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData: debounce(function () {
      this.$axios
        .get(`${this.$url.tests}?page=${this.pager.page}&limit=10&keyword=${this.filter.keyword}&category=${this.filter.category.toString()}`)
        .then((resp) => {
          this.tests = resp.data.results;
          this.pager.total = resp.data.total;
          this.loading = false;
        })
    }, config.debounceDelay),

    toClick(index) {
      this.filter.category.splice(index + 1); 
      this.getData();
    },
    toCategoryChange(data) {
      this.filter.category = data;
      this.getData();
    },
    toKeywordChange(data) {
      this.filter.keyword = data;
      this.getData();
    },
    toPage(page) {
      this.pager.page = page;
      this.getData();
    },
    toWorkshop() {
      this.$router.push('/workshop/tests');
    },
  },
};
</script>

<style lang="scss" scoped>
.tests-filter {
  width: 16em;
  border-right: 1px solid silver;
}
</style>
