<template>
  <div class="flex-column pd-1">
    <form class="pure-form">
      <input
      type="text"
      placeholder="Search"
      class="w-100"
      v-model="keyword"
      >
    </form>
    <tree  
    class="mt-1"
    :data="category"
    @click="toClick"
    />
    </div>
  </template>
  <script>
  export default {
    data() {
      return {
        keyword: '',
        category: [],
      };
    },
    watch: {
      keyword(value) {
        this.$emit('keywordChange', value);
      },
    },
    mounted() {
      this.$axios.get(this.$url.category)
        .then((resp) => {
        this.category = resp.data;
      })
      .catch((err) => {
        this.$alert('error', err.message);
      });
  },
  methods: {
    toClick(data) {
      this.$emit('categoryChange', data);
    }
  },
};
</script>
